// Preview iframe(s) url: https://mobiledx-mkt-prod3-m.adobe-campaign.com/xtk/acritePreview.jssp

setInterval( function() {
  var emailSrc = document.firstElementChild.outerHTML

  window.top.postMessage({
    method: 'checklist-adobeCampaignEmailDesignerPreviewSendContent',
    content: emailSrc},
    '*'
  );
}, 5000 )



